'use client';

import React, { useState, useEffect } from 'react';
import { Container, Title, Text, Button, Group, Box, Badge, Modal, ActionIcon, Grid, Paper, LoadingOverlay, Transition } from '@mantine/core';
import { IconChevronLeft, IconChevronRight, IconX, IconClock, IconMapPin, IconCalendarEvent } from '@tabler/icons-react';
import { format, startOfMonth, endOfMonth, startOfWeek, endOfWeek, addDays, isSameMonth, isSameDay, isToday, parseISO } from 'date-fns';
import { ko } from 'date-fns/locale';
import { fetchEvents } from '../../src/services/googleCalendar';
import { motion, AnimatePresence } from 'framer-motion';

export default function Schedule() {
    const [currentMonth, setCurrentMonth] = useState(new Date());
    const [selectedDate, setSelectedDate] = useState(new Date());
    const [events, setEvents] = useState([]);
    const [holidays, setHolidays] = useState({});
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const [isModalOpen, setIsModalOpen] = useState(false);

    // Korean Public Holidays (Fixed dates for demo)
    const getHolidays = (year) => {
        return {
            [`${year}-01-01`]: '신정',
            [`${year}-03-01`]: '삼일절',
            [`${year}-05-05`]: '어린이날',
            [`${year}-06-06`]: '현충일',
            [`${year}-08-15`]: '광복절',
            [`${year}-10-03`]: '개천절',
            [`${year}-10-09`]: '한글날',
            [`${year}-12-25`]: '크리스마스',
        };
    };

    useEffect(() => {
        const year = currentMonth.getFullYear();
        setHolidays(getHolidays(year));
        loadEvents();
    }, [currentMonth]);

    const loadEvents = async () => {
        setLoading(true);
        setError(null);
        try {
            const start = startOfWeek(startOfMonth(currentMonth));
            const end = endOfWeek(endOfMonth(currentMonth));
            const fetchedEvents = await fetchEvents(start, end);
            setEvents(fetchedEvents);
        } catch (error) {
            console.error("Failed to load events", error);
            setError(error.message);
        } finally {
            setLoading(false);
        }
    };

    const nextMonth = () => {
        setCurrentMonth(addDays(endOfMonth(currentMonth), 1));
    };

    const prevMonth = () => {
        setCurrentMonth(addDays(startOfMonth(currentMonth), -1));
    };

    const onDateClick = (day) => {
        setSelectedDate(day);
        setIsModalOpen(true);
    };

    const renderHeader = () => {
        const dateFormat = "yyyy년 M월";

        return (
            <Group justify="space-between" mb="xl">
                <Title order={2} style={{ fontFamily: 'var(--font-sf)', fontWeight: 700 }}>
                    {format(currentMonth, dateFormat, { locale: ko })}
                </Title>
                <Group>
                    <ActionIcon variant="subtle" color="gray" size="lg" onClick={prevMonth}>
                        <IconChevronLeft size={24} />
                    </ActionIcon>
                    <Button variant="light" color="gray" onClick={() => setCurrentMonth(new Date())}>
                        오늘
                    </Button>
                    <ActionIcon variant="subtle" color="gray" size="lg" onClick={nextMonth}>
                        <IconChevronRight size={24} />
                    </ActionIcon>
                </Group>
            </Group>
        );
    };

    const renderDays = () => {
        const days = ["일", "월", "화", "수", "목", "금", "토"];
        return (
            <div style={{ display: 'grid', gridTemplateColumns: 'repeat(7, 1fr)', marginBottom: '0.5rem' }}>
                {days.map((day, i) => (
                    <Text
                        key={i}
                        ta="center"
                        fw={600}
                        c={i === 0 ? 'red' : i === 6 ? 'blue' : 'dimmed'}
                        size="sm"
                    >
                        {day}
                    </Text>
                ))}
            </div>
        );
    };

    const renderCells = () => {
        const monthStart = startOfMonth(currentMonth);
        const monthEnd = endOfMonth(monthStart);
        const startDate = startOfWeek(monthStart);
        const endDate = endOfWeek(monthEnd);

        // Always show 6 weeks to keep size constant
        const endDateFixed = addDays(startDate, 41);

        const rows = [];
        let days = [];
        let day = startDate;

        while (day <= endDateFixed) {
            for (let i = 0; i < 7; i++) {
                const cloneDay = day;
                const formattedDate = format(day, "d");
                const isHoliday = holidays[format(day, 'yyyy-MM-dd')];
                const dayEvents = events.filter(e => isSameDay(e.start, day));
                const isSelected = isSameDay(day, selectedDate);
                const isCurrentMonth = isSameMonth(day, monthStart);

                days.push(
                    <div
                        key={day}
                        onClick={() => onDateClick(cloneDay)}
                        style={{
                            height: '140px', // Fixed height increased
                            padding: '8px',
                            borderTop: '1px solid var(--mantine-color-default-border)',
                            borderRight: i === 6 ? 'none' : '1px solid var(--mantine-color-default-border)',
                            backgroundColor: isSelected ? 'rgba(0, 122, 255, 0.05)' : isCurrentMonth ? 'var(--mantine-color-body)' : 'var(--mantine-color-gray-0)',
                            cursor: 'pointer',
                            color: !isCurrentMonth ? 'var(--mantine-color-gray-4)' : 'inherit',
                            position: 'relative',
                            transition: 'background-color 0.2s',
                            display: 'flex',
                            flexDirection: 'column',
                        }}
                        className="calendar-cell"
                    >
                        <Group justify="space-between" align="flex-start" mb="xs">
                            <Text
                                size="sm"
                                fw={isToday(day) ? 700 : 500}
                                c={i === 0 ? 'red' : i === 6 ? 'blue' : 'inherit'}
                                style={isToday(day) ? {
                                    backgroundColor: '#007AFF', color: 'white',
                                    borderRadius: '50%', width: '24px', height: '24px',
                                    display: 'flex', alignItems: 'center', justifyContent: 'center'
                                } : {}}
                            >
                                {formattedDate}
                            </Text>
                            {isHoliday && <Badge color="red" size="xs" variant="light">{isHoliday}</Badge>}
                        </Group>
                        <Box style={{ flex: 1, overflowY: 'auto' }}>
                            {dayEvents.slice(0, 4).map((event, idx) => (
                                <Box
                                    key={idx}
                                    style={{
                                        backgroundColor: event.color || 'var(--mantine-color-blue-1)',
                                        borderRadius: '4px',
                                        padding: '2px 4px',
                                        marginBottom: '2px',
                                        fontSize: '11px',
                                        whiteSpace: 'nowrap',
                                        overflow: 'hidden',
                                        textOverflow: 'ellipsis',
                                        color: 'var(--mantine-color-blue-9)'
                                    }}
                                >
                                    {event.title}
                                </Box>
                            ))}
                            {dayEvents.length > 4 && (
                                <Text size="xs" c="dimmed">+{dayEvents.length - 4} more</Text>
                            )}
                        </Box>
                    </div>
                );
                day = addDays(day, 1);
            }
            rows.push(
                <div key={day} style={{ display: 'grid', gridTemplateColumns: 'repeat(7, 1fr)', borderLeft: '1px solid var(--mantine-color-default-border)', borderBottom: '1px solid var(--mantine-color-default-border)' }}>
                    {days}
                </div>
            );
            days = [];
        }
        return <Box style={{ border: '1px solid var(--mantine-color-default-border)', borderRadius: '12px', overflow: 'hidden', boxShadow: 'var(--mantine-shadow-sm)' }}>{rows}</Box>;
    };

    const selectedDayEvents = events.filter(e => isSameDay(e.start, selectedDate));
    const holidayName = holidays[format(selectedDate, 'yyyy-MM-dd')];

    return (
        <Container size="xl" py="xl">
            {renderHeader()}
            <Box pos="relative">
                <LoadingOverlay visible={loading} overlayProps={{ radius: "sm", blur: 2 }} />
                {error && <Text c="red" ta="center" mb="md">{error}</Text>}

                {/* Desktop Calendar View */}
                {renderDays()}
                {renderCells()}
            </Box>

            {/* Event Modal */}
            <Modal
                opened={isModalOpen}
                onClose={() => setIsModalOpen(false)}
                title={
                    <Group>
                        <Text size="lg" fw={700}>
                            {format(selectedDate, 'M월 d일')} ({format(selectedDate, 'EEEE', { locale: ko })})
                        </Text>
                        {holidayName && <Badge color="red">{holidayName}</Badge>}
                    </Group>
                }
                centered
                size="md"
                radius="lg"
            >
                <div style={{ minHeight: '300px' }}>
                    {selectedDayEvents.length > 0 ? (
                        <Box>
                            {selectedDayEvents.map((event, idx) => (
                                <Paper key={idx} shadow="xs" p="md" mb="sm" radius="md" withBorder>
                                    <Group justify="space-between" mb="xs">
                                        <Badge color={event.allDay ? "indigo" : "blue"} variant="light">
                                            {event.allDay ? "종일" : `${format(event.start, 'HH:mm')} ~ ${format(event.end, 'HH:mm')}`}
                                        </Badge>
                                        {event.location && (
                                            <Group gap="xs">
                                                <IconMapPin size={14} color="gray" />
                                                <Text size="xs" c="dimmed">{event.location}</Text>
                                            </Group>
                                        )}
                                    </Group>
                                    <Text fw={600} size="md" mb="xs">{event.title}</Text>
                                    {event.description && <Text size="sm" c="dimmed">{event.description}</Text>}
                                </Paper>
                            ))}
                        </Box>
                    ) : (
                        <Box style={{ display: 'flex', flexDirection: 'column', alignItems: 'center', justifyContent: 'center', height: '200px', opacity: 0.5 }}>
                            <IconCalendarEvent size={48} stroke={1.5} />
                            <Text mt="sm">등록된 일정이 없습니다.</Text>
                        </Box>
                    )}
                </div>
            </Modal>
        </Container>
    );
}
